<script>
  import notes from "./notes.js";
  import buttons from "./buttons.js";
  import { fade } from "svelte/transition";

  import Button from "./Button.svelte";

  const noteTypes = ["natural", "sharp", "flat"];

  const noteImg = {
    natural: "img/wholenote.png",
    sharp: "img/sharpnote.png",
    flat: "img/flatnote.png"
  };

  const defaults = () => {
    return {
      natural: [],
      sharp: [],
      flat: []
    };
  };

  let paintNotes = defaults();
  const drawNotes = note => {
    paintNotes = defaults();
    notes
      .filter(n => n.note === note)
      .forEach(n => {
        paintNotes[n.type].push(n.pos);
      });
  };
</script>

<style>
  main {
    width: 800px;
    margin: 0 auto;
  }
  h1 {
    color: #ff3e00;
    text-transform: uppercase;
    font-size: 4em;
    font-weight: 100;
  }
  .staff {
    position: relative;
  }
  .controls {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }
</style>

<main>
  <h1>Soprano</h1>
  <div class="staff">
    <img src="img/staff.png" alt="image of staff" />
    {#each noteTypes as type}
      {#each paintNotes[type] as notePos (notePos.top)}
        <img
          transition:fade
          src={noteImg[type]}
          alt="image of a {type} note"
          style="position:absolute; top: {notePos.top}px; left: {notePos.left}px;" />
      {/each}
    {/each}
  </div>
  <div class="controls">
    {#each buttons as button}
      <Button
        handleClick={() => {
          drawNotes(button.key);
        }}
        label={button.label} />
    {/each}
    <Button
      label="*"
      handleClick={() => {
        paintNotes = defaults();
      }} />
  </div>
</main>
